/* ==========================================================================
   #FORM
   ========================================================================== */

form {
  font-size: 0.909rem;

  > div,
  > li {
    margin-bottom: 1rem;
  }
}

input {
  &:not([type='checkbox']):not([type='radio']):not([type='submit']) {
    border-width: 0 0 1px;
    border-style: solid;
    border-color: $color-crimson;
    width: 100%;
    height: 40px;

    &:focus {
      outline: none;
      border-bottom-width: 2px;
      border-bottom-color: darken($color-crimson, 5%);
    }
  }

  &[type='submit'] {
    border: 0;
    padding: 0.3rem 1rem;
    cursor: pointer;
    background: none;
    width: 100%;
    max-width: 125px;
    font-size: 1rem;
  }
}

textarea {
  border: 1px solid $color-crimson;
  width: 100%;
  margin-top: 0.8rem;
  padding: 15px;

  &:focus {
    outline: none;
    border: 2px solid darken($color-crimson, 5%);
  }
}

label {
  color: $color-eternity;
  text-transform: uppercase;
  font-size: 0.54545rem;
  letter-spacing: 1px;
}

.c-form {
  &__submit-wrap {
    position: relative;
    text-align: right;

    &.c-link--chevron {
      &::after {
        position: absolute;
        left: auto;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
      }
    }

    input {
      position: relative;
      z-index: 10;
    }
  }
}
